.root {
  width: 100%;
  padding: 6% 6% 0;
  font-family: var(--font-main);
  margin-bottom: 2%;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: var(--space-md);
  margin-bottom: var(--space-xl);
}

.title {
  font-size: var(--fs-lg);
  font-weight: var(--fw-bold);
  color: var(--tx-title);
  margin: 0;
}

.sub {
  margin-top: var(--space-sm);
  color: var(--text-light);
  font-size: var(--fs-xs);
}

.more {
  color: var(--tx-link);
  font-weight: var(--fw-semibold);
}

.leaderboardBtn {
  background: linear-gradient(
    90deg,
    var(--primary-color),
    var(--primary-color-hover)
  );
  color: var(--white);
  padding: var(--space-md) var(--space-lg);
  border-radius: 28px;
  border: none;
  font-weight: var(--fw-bold);
  cursor: pointer;
  box-shadow: 0 6px 20px rgba(127, 0, 255, 0.18);
}

.row {
  width: 100%;
  display: flex;
  justify-content: center;
}

.list {
  display: flex;
  flex-wrap: wrap;
  gap: var(--space-lg);
  width: 100%;
}

.card {
  width: clamp(90px, 20vw, 130px);
  text-align: center;
  flex: 1 1 120px;
}

.avatarWrap {
  width: clamp(65px, 12vw, 85px);
  height: clamp(65px, 12vw, 85px);
  border-radius: 50%;
  overflow: hidden;
  margin: 0 auto var(--space-sm);
  box-shadow: 0 6px 20px rgba(50, 50, 80, 0.08);
  background: var(--white);
}

.avatar {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.name {
  font-weight: var(--fw-bold);
  font-size: var(--fs-sm);
  color: var(--tx-title);
}

.events {
  color: var(--text-light);
  font-size: var(--fs-xs);
}
