:root {
  --accent: #7f2bff;
  --card-width: 260px;
  --card-height: 360px;
  --category-card-width: 285px;
  --category-card-height: 159px;
  --hover-duration: 1.4s;
}

.dashboard-root {
  min-height: 100vh;
  -webkit-font-smoothing: antialiased;
  width: 100%;
  background:
    radial-gradient(circle at 2% 2%, rgba(233, 215, 255, 0.55) 0%, rgba(233, 215, 255, 0.25) 20%, transparent 65%),
    radial-gradient(circle at 85% 38%, rgba(255, 245, 220, 0.60) 0%, rgba(225, 200, 255, 0.35) 20%, rgba(225, 200, 255, 0.18) 30%, transparent 75%),
    radial-gradient(circle at 12% 95%, rgba(235, 220, 255, 0.35) 0%, transparent 40%),
    #ffffff;
}

.dash-hero {
  max-width: 1200px;
  margin: 30px auto 0;
  text-align: center;
  padding: 0 24px;
  margin-top: 0px;
}

.btn-explore {
  background: var(--accent);
  color: white;
  padding: 12px 28px;
  border-radius: 28px;
  border: none;
  font-weight: 600;
  cursor: pointer;
  margin-top: 24px;
}

.hero-carousel-area {
  position: relative;
  width: 100%;
}

.card-carousel {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 64px 0 0;
}

.cards-stage {
  position: relative;
  width: 100%;
  max-width: 1100px;
  height: 420px;
  display: flex;
  align-items: center;
  justify-content: center;
  pointer-events: none;
}

.card-item {
  position: absolute;
  width: var(--card-width);
  height: var(--card-height);
  border-radius: 14px;
  overflow: hidden;
  transition: transform 450ms cubic-bezier(.2, .9, .2, 1),
    filter 400ms ease,
    opacity 400ms ease;
  box-shadow: 0 12px 30px rgba(15, 15, 15, 0.12);
  cursor: pointer;
  pointer-events: auto;
  background: #fff;
  user-select: none;
}

.card-item img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: 10px;
}

.college-event-works-section-title {
  text-align: center;
  font-size: 32px;
  font-weight: 700;
  margin-top: 30px;
}

.animation {
  width: 100%;
}

.pos-hidden {
  opacity: 0;
  transform: translateX(0) scale(.7);
  filter: blur(6px);
  z-index: 0;
}

.pos-left-xfar {
  transform: translateX(-600px) scale(.65) rotate(-15deg);
  filter: blur(8px);
  opacity: .35;
  z-index: 1;
}

.pos-left-far {
  transform: translateX(-420px) scale(.78) rotate(-12deg);
  filter: blur(6px);
  opacity: .55;
  z-index: 2;
}

.pos-left {
  transform: translateX(-210px) scale(.9) rotate(-8deg);
  filter: blur(3px);
  opacity: .85;
  z-index: 3;
}

.pos-center {
  transform: translateX(0) scale(1.12);
  filter: blur(0);
  opacity: 1;
  z-index: 10;
  box-shadow: 0 22px 60px rgba(15, 15, 15, 0.25);
}

.pos-right {
  transform: translateX(210px) scale(.9) rotate(8deg);
  filter: blur(3px);
  opacity: .85;
  z-index: 3;
}

.pos-right-far {
  transform: translateX(420px) scale(.78) rotate(12deg);
  filter: blur(6px);
  opacity: .55;
  z-index: 2;
}

.pos-right-xfar {
  transform: translateX(600px) scale(.65) rotate(15deg);
  filter: blur(8px);
  opacity: .35;
  z-index: 1;
}

@media (max-width: 900px) {
  :root {
    --card-width: 200px;
    --card-height: 280px;
  }

  .cards-stage {
    height: 320px;
  }

  .pos-left-xfar {
    transform: translateX(-360px) scale(.6) rotate(-12deg);
  }

  .pos-left-far {
    transform: translateX(-250px) scale(.7) rotate(-10deg);
  }

  .pos-left {
    transform: translateX(-130px) scale(.9) rotate(-6deg);
  }

  .pos-right {
    transform: translateX(130px) scale(.9) rotate(6deg);
  }

  .pos-right-far {
    transform: translateX(250px) scale(.7) rotate(10deg);
  }

  .pos-right-xfar {
    transform: translateX(360px) scale(.6) rotate(12deg);
  }

  .pos-center {
    transform: scale(1.05);
  }
}

.search-card {
  width: 82%;
  max-width: 980px;
  position: relative;
  z-index: 99;
  backdrop-filter: blur(12px);
  border-radius: 18px;
  padding: 20px;
  margin-top: -30px;
  box-shadow: 0 3px 5px rgba(0, 0, 0, 0.04), 0 12px 22px rgba(127, 43, 255, 0.10);
  border: 2px solid rgba(150, 78, 255, 0.12);
}

.search-top {
  display: flex;
  gap: 28px;
  flex-wrap: wrap;
  justify-content: space-around;
  padding-bottom: 12px;
  border-bottom: 1px solid rgba(150, 78, 255, 0.12);
}

.chip {
  display: flex;
  align-items: center;
  gap: 8px;
  border: none;
  padding: 8px 12px;
  border-radius: 22px;
  background: transparent;
  cursor: pointer;
  font-weight: 600;
  font-size: 14px;
  color: #333;
}

.chip.active {
  background: var(--accent);
  color: #fff;
  box-shadow: 0 4px 12px rgba(127, 43, 255, 0.18);
}

.chip.active svg path {
  fill: white !important;
}

.search-bottom {
  display: flex;
  justify-content: space-around;
  gap: 28px;
  align-items: center;
  flex-wrap: wrap;
}

.filter-col {
  display: flex;
  gap: 12px;
  align-items: center;
  min-width: 180px;
}

.filter-title {
  font-weight: 700;
}

.filter-sub {
  font-size: 13px;
  color: #777;
  margin-top: 3px;
}

.Choose-Category-title {
  padding-top: 8%;
  padding-left: 6%;
}

.category-wrapper {
  width: 100%;
  padding: 0 6%;
  display: grid;
  gap: 22px;
  grid-template-columns: repeat(4, 1fr);
  align-items: stretch;
  margin-top: 5%;
}

.category-card {
  height: var(--category-card-height);
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  background: white;
  cursor: pointer;
  position: relative;
  border: 1px solid rgba(0, 0, 0, 0.04);
  transition: background .45s ease;
  --cat-bg: #f5f5f5;
}

.category-card::before {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--cat-bg);
  transform: translateX(-100%);
  transition: transform var(--hover-duration) cubic-bezier(.25, .8, .25, 1);
  z-index: 0;
}

.category-card:hover::before {
  transform: translateX(0);
}

.category-card * {
  position: relative;
  z-index: 2;
}

.category-card .card-left {
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--cat-bg);
  transition: transform .45s ease, background .35s ease;
}

.category-card .card-left img {
  width: 75%;
  height: auto;
  object-fit: contain;
}

.category-card .card-right {
  width: 55%;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px;
  font-size: 20px;
  font-weight: 600;
  color: #111;
  transition: transform .45s ease, color .3s ease;
}

.category-card:hover .card-left,
.category-card:hover .card-right {
  background: transparent !important;
}

.category-card:hover .card-right {
  transform: translateX(6px);
}

.conference {
  --cat-bg: #ffe3bf;
}

.hackathon {
  --cat-bg: #bde6ff;
}

.webinar {
  --cat-bg: #ead7ff;
}

.athletics {
  --cat-bg: #ffd4d4;
}

.concerts {
  --cat-bg: #c9e7ff;
}

.tournaments {
  --cat-bg: #ffeab5;
}

.jobfairs {
  --cat-bg: #ffd0e4;
}

.explore {
  --cat-bg: #e3d8ff;
}

:root {
  --hover-duration: 1.6s;
}

.subscribe-section {
  width: 100%;
  display: flex;
  justify-content: center;
  margin: 60px 0;
}

.subscribe-card {
  width: 75%;
  border-radius: 35px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  animation: fadeIn 0.4s ease;
}

.subscribe-card.whatsapp {
  background: #1f5d27;
  color: white;
}

.subscribe-card.email {
  background: #23336b;
  color: white;
  padding-bottom: 50px;
}

.subscribe-left {
  width: 40%;
  display: flex;
  justify-content: center;
}

.sub-img {
  width: 100%;
  max-width: 330px;
  margin-top: -10px;
}

.sub-img-email {
  width: 100%;
  max-width: 330px;
  margin-top: -83px;
}

.subscribe-right {
  width: 55%;
}

.sub-title {
  font-size: 25px;
  font-weight: 700;
  line-height: 1.4;
  margin-bottom: 22px;
}

.sub-input-wrap {
  display: flex;
  align-items: center;
  border-radius: 38.644px;
  border: 1.208px solid rgba(255, 255, 255, 0.20);
  background: #0A4D06;
  max-width: 493px;
  padding: 0px 5px 0px 14px;
}

.sub-input-wrap-email {
  display: flex;
  align-items: center;
  border-radius: 38.644px;
  border: 1.208px solid rgba(255, 255, 255, 0.20);
  background: #27305D;
  max-width: 493px;
  padding: 0px 0px 0px 14px;
}


.sub-input-icon {
  font-size: 22px;
  margin-right: 12px;
}

.sub-input {
  flex: 1;
  border: none;
  outline: none;
  font-size: 18px;
  background: transparent;
  color: #fff;
}

.sub-btn {
  background: white;
  padding: 12px 24px;
  border-radius: 40px;
  border: none;
  font-size: 18px;
  font-weight: 700;
  cursor: pointer;
}

.switch-icon {
  margin-left: 20px;
  cursor: pointer;
  background: #0A4D06;
  padding: 12px;
  border-radius: 50%;
  transition: 0.3s;
}

.switch-icon:hover {
  transform: scale(1.1);
}

.sub-desc {
  margin-top: 18px;
  font-size: 12px;
  opacity: 0.9;
}

@media (max-width: 900px) {
  .subscribe-card {
    flex-direction: column;
    text-align: center;
  }

  .subscribe-left,
  .subscribe-right {
    width: 100%;
  }

  .sub-input-wrap {
    margin: 0 auto;
  }

  .sub-title {
    font-size: 28px;
  }
}

@keyframes fadeIn {
  0% {
    opacity: 0;
    transform: translateY(20px);
  }

  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.cards-stage {
  max-width: 1100px;
  margin: 0 auto;
}

@media (max-width: 1200px) {

  .pos-left,
  .pos-right {
    transform: translateX(-170px) scale(.95) rotate(-6deg);
  }
}

@media (max-width: 900px) {
  :root {
    --card-width: 200px;
    --card-height: 280px;
  }

  .cards-stage {
    height: 320px;
  }

  .card-item {
    transition: transform 520ms cubic-bezier(.2, .9, .2, 1), opacity 360ms ease;
  }
}


@media (max-width: 992px) {
  .category-wrapper {
    grid-template-columns: repeat(2, 1fr);
    padding: 0 5%;
  }

  .dash-title {
    font-size: 40px;
    padding-top: 64px;
  }
}

@media (max-width: 720px) {
  .category-wrapper {
    grid-template-columns: 1fr;
    padding: 0 6%;
  }

  .category-card {
    height: 150px;
  }

  .category-card .card-right {
    font-size: 18px;
  }

  .search-card {
    width: 94%;
    padding: 14px;
    margin-top: 8px;
  }

  .search-bottom {
    flex-direction: column;
    align-items: stretch;
    gap: 12px;
  }
}

@media (max-width: 900px) {
  :root {
    --card-width: 180px;
    --card-height: 250px;
  }

  .cards-stage {
    height: 300px;
    transform: scale(0.95);
  }

  .pos-left-xfar {
    transform: translateX(-260px) scale(0.55) rotate(-10deg);
  }

  .pos-left-far {
    transform: translateX(-180px) scale(0.65) rotate(-8deg);
  }

  .pos-left {
    transform: translateX(-90px) scale(0.85) rotate(-5deg);
  }

  .pos-right {
    transform: translateX(90px) scale(0.85) rotate(5deg);
  }

  .pos-right-far {
    transform: translateX(180px) scale(0.65) rotate(8deg);
  }

  .pos-right-xfar {
    transform: translateX(260px) scale(0.55) rotate(10deg);
  }

  .pos-center {
    transform: scale(1.05);
  }
}

@media (max-width: 600px) {
  :root {
    --card-width: 150px;
    --card-height: 210px;
  }

  .card-item img {
    padding: 18px;
  }
}

@media (max-width: 1400px) and (min-width: 901px) {

  :root {
    --card-width: 230px;
    --card-height: 320px;
  }

  .cards-stage {
    height: 360px;
    transform: scale(0.98);
  }

  .pos-left-xfar {
    transform: translateX(-480px) scale(0.70) rotate(-12deg);
  }

  .pos-left-far {
    transform: translateX(-330px) scale(0.80) rotate(-10deg);
  }

  .pos-left {
    transform: translateX(-170px) scale(0.95) rotate(-6deg);
  }

  .pos-right {
    transform: translateX(170px) scale(0.95) rotate(6deg);
  }

  .pos-right-far {
    transform: translateX(330px) scale(0.80) rotate(10deg);
  }

  .pos-right-xfar {
    transform: translateX(480px) scale(0.70) rotate(12deg);
  }

  .pos-center {
    transform: translateX(0px) scale(1.08);
  }

  .card-item img {
    object-fit: contain;
    padding: 10px;
  }
}

@media (max-width: 1151px) and (min-width: 901px) {

  :root {
    --card-width: 210px;
    --card-height: 300px;
  }

  .cards-stage {
    height: 330px;
    transform: scale(0.96);
  }

  .pos-left-xfar {
    transform: translateX(-380px) scale(0.68) rotate(-12deg);
  }

  .pos-left-far {
    transform: translateX(-260px) scale(0.78) rotate(-9deg);
  }

  .pos-left {
    transform: translateX(-135px) scale(0.92) rotate(-6deg);
  }

  .pos-center {
    transform: translateX(0) scale(1.06);
  }

  .pos-right {
    transform: translateX(135px) scale(0.92) rotate(6deg);
  }

  .pos-right-far {
    transform: translateX(260px) scale(0.78) rotate(9deg);
  }

  .pos-right-xfar {
    transform: translateX(380px) scale(0.68) rotate(12deg);
  }

  .card-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
  }
}

.category-card:hover { box-shadow: 0 18px 50px rgba(15,15,15,0.12); transform: translateY(-4px); transition: transform .35s ease, box-shadow .35s ease; }

.role-modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.12);
  backdrop-filter: blur(4px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 3000;

  opacity: 0;
  animation: overlayFade 0.5s ease forwards;
}

.role-modal-overlay > div {
  opacity: 0;
  transform: translateY(-40px) scale(0.95);
  animation: modalSlideDown 0.7s cubic-bezier(0.16, 1, 0.3, 1) forwards;
  animation-delay: 0.1s;
}

@keyframes overlayFade {
  0% {
    opacity: 0;
    backdrop-filter: blur(0px);
  }
  100% {
    opacity: 1;
    backdrop-filter: blur(4px);
  }
}

@keyframes modalSlideDown {
  0% {
    opacity: 0;
    transform: translateY(-40px) scale(0.95);
  }
  60% {
    opacity: 1;
    transform: translateY(5px) scale(1.02); 
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

