.root {
  width: 100%;
  padding: 60px 6%;
  text-align: center;
  font-family: var(--font-main);
}

/* Tabs */
.tabs {
  display: flex;
  justify-content: center;
  gap: 16px;
  margin-bottom: 14px;
}

.tab {
  padding: 10px 26px;
  border-radius: 999px;
  border: 1px solid #ddd;
  background: #fff;
  font-size: var(--fs-sm);
  font-weight: var(--fw-semibold);
  cursor: pointer;
  transition: all 0.25s ease;
}

.active {
  background: var(--primary-color);
  color: #fff;
  border-color: var(--primary-color);
  box-shadow: 0 6px 16px rgba(127, 0, 255, 0.25);
}

/* Subtitle */
.sub {
  font-size: var(--fs-sm);
  color: var(--text-light);
  margin-top: var(--space-sm);
}

/* Grid */
.grid {
  margin: 50px auto 0;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 50px;
}

/* Card */
.card {
  padding: 20px 32px;
  border-radius: 18px;
  border: 1.5px solid #888;
  background: #fff;
  display: flex;
  align-items: center;
  gap: 36px;
  transition: transform 0.25s ease, box-shadow 0.25s ease;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
}

/* Image */
.img {
  width: 90px;
  height: 90px;
  border-radius: 50%;
  object-fit: cover;
}

/* Text */
.text {
  text-align: left;
}

.name {
  font-size: var(--fs-md);
  font-weight: var(--fw-semibold);
  color: var(--tx-title);
}

.events {
  margin-top: 4px;
  font-size: var(--fs-xs);
  color: var(--text-light);
}

/* Responsive */
@media (max-width: 1000px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
    gap: 32px;
  }
}

@media (max-width: 620px) {
  .grid {
    grid-template-columns: 1fr;
  }

  .card {
    flex-direction: column;
    text-align: center;
  }

  .text {
    text-align: center;
  }
}
