:root {
  --carousel-card-width: 260px;
  --carousel-card-height: 360px;
}

/* ROOT */
.carouselRoot {
  width: 100%;
  display: flex;
  justify-content: center;
  padding-top: var(--space-xl);
}

/* STAGE */
.cardsStage {
  position: relative;
  width: 100%;
  max-width: 1100px;
  height: 420px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* CARD */
.cardItem {
  position: absolute;
  width: var(--carousel-card-width);
  height: var(--carousel-card-height);
  border-radius: 14px;
  overflow: hidden;
  background: #fff;
  cursor: pointer;
  transition: transform 450ms ease, opacity 400ms ease, filter 400ms ease;
  box-shadow: 0 12px 30px rgba(0, 0, 0, 0.12);
}

.cardItem img {
  width: 100%;
  height: 100%;
  object-fit: contain;
  padding: var(--space-sm);
}

/* POSITIONS */
.posHidden { opacity: 0; transform: scale(0.7); filter: blur(6px); }
.posLeftXFar { transform: translateX(-600px) scale(0.65) rotate(-15deg); opacity: .35; }
.posLeftFar { transform: translateX(-420px) scale(.78) rotate(-12deg); opacity: .55; }
.posLeft { transform: translateX(-210px) scale(.9) rotate(-8deg); opacity: .85; }
.posCenter { transform: scale(1.12); z-index: 10; }
.posRight { transform: translateX(210px) scale(.9) rotate(8deg); opacity: .85; }
.posRightFar { transform: translateX(420px) scale(.78) rotate(12deg); opacity: .55; }
.posRightXFar { transform: translateX(600px) scale(.65) rotate(15deg); opacity: .35; }

/* RESPONSIVE */
@media (max-width: 900px) {
  :root {
    --carousel-card-width: 200px;
    --carousel-card-height: 280px;
  }
  .cardsStage { height: 320px; }
}
