/* ================= ROOT ================= */

.spotlight-root {
  width: 100%;
  font-family: var(--font-main);
}

/* ================= TITLE ================= */

.spotlight-title {
  font-size: var(--fs-xl);
  font-weight: var(--fw-bold);
  margin-top: 2%;
  margin-bottom: 3%;
  text-align: center;
  color: var(--tx-title);
}

/* ================= SLIDER ================= */

.spotlight-slider-wrapper {
  width: 100%;
  overflow: hidden;
  position: relative;
}

.spotlight-slider-track {
  display: flex;
  width: 100%;
  transition: transform 2.2s ease;
}

/* ================= SLIDE ================= */

.spotlight-slide {
  min-width: 100%;
  display: flex;
  gap: 70px;
  padding: 3% 10%;
  background: #edf7ff;
}

/* ================= LEFT ================= */

.slide-left {
  width: 40%;
}

.slide-image {
  width: 100%;
  height: 270px;
  border-radius: 16px;
  object-fit: cover;
}

/* ================= RIGHT ================= */

.slide-right {
  width: 60%;
  display: flex;
  flex-direction: column;
}

.slide-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.slide-title {
  font-size: var(--fs-lg);
  font-weight: var(--fw-bold);
  color: var(--tx-title);
}

.share-btn {
  font-size: var(--fs-lg);
  background: none;
  border: none;
  cursor: pointer;
}

/* ================= META ================= */

.meta {
  margin-top: var(--space-md);
}

.meta-row {
  display: flex;
  align-items: center;
  gap: var(--space-sm);
  margin: 6px 0;
}

.meta-icon {
  font-size: var(--fs-md);
}

.meta-text {
  color: var(--text-light);
  font-size: var(--fs-sm);
}

/* ================= COUNTDOWN ================= */

.starts-in {
  margin-top: var(--space-lg);
  font-weight: var(--fw-semibold);
  color: var(--tx-sub-title);
}

.countdown-row {
  display: flex;
  gap: var(--space-md);
  margin: var(--space-md) 0;
}

.count-pill {
  width: 83px;
  height: 70px;
  text-align: center;
  padding: 10px 0;
  border-radius: 50%;
  border: 2px solid;
}

/* Individual pills (intentional color identity) */

#days {
  background-color: #dca0ff;
  border-color: #830dc8;
}

#hours {
  background-color: #d1f8db;
  border-color: #0b822a;
}

#mins {
  background-color: #f5dba2;
  border-color: #f2b634;
}

#sec {
  background-color: #6cb4f9;
  border-color: #0052a2;
}

.count-num {
  font-size: var(--fs-lg);
  font-weight: var(--fw-bold);
  color: var(--text-dark);
}

.count-label {
  font-size: var(--fs-xs);
  color: var(--text-light);
}

/* ================= CTA ================= */

.btn-get {
  width: 160px;
  padding: var(--space-sm);
  background: var(--btn-primary);
  color: var(--white);
  border-radius: 25px;
  border: none;
  margin-top: var(--space-lg);
  font-weight: var(--fw-semibold);
  cursor: pointer;
  transition: background 0.25s ease;
}

.btn-get:hover {
  background: var(--btn-primary-hover);
}

/* ================= CONTROLS ================= */

.spotlight-controls {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: var(--space-xl);
  gap: var(--space-md);
}

.arrow {
  width: 42px;
  height: 42px;
  border-radius: 50%;
  border: none;
  background: #cce8ff;
  font-size: var(--fs-lg);
  cursor: pointer;
  transition: background 0.25s ease;
}

.arrow:hover {
  background: #b5ddff;
}

/* ================= DOTS ================= */

.dots {
  display: flex;
  gap: var(--space-sm);
}

.dot {
  width: 13px;
  height: 7px;
  border-radius: 10px;
  background: var(--tx-placeholder);
  border: none;
  cursor: pointer;
}

.dot.active {
  background: #8d8989;
}
