.root {
  color: #fff;
  font-family: "Poppins", sans-serif;
}

/* Skyline */
.skyline img {
  width: 100%;
  display: block;
}

/* Main */
.main {
  padding: 50px 8% 30px;
  background-color: #000;
}

/* Top */
.top {
  display: flex;
  justify-content: space-between;
  gap: 15%;
}

/* Brand */
.brand {
  max-width: 420px;
}

.logo {
  width: 50%;
}

.text {
  margin: 18px 0;
  font-size: 17px;
  line-height: 1.6;
  color: #d0d0d0;
}

/* Subscribe */
.subscribe {
  flex: 1;
  border: 1px solid #3a3a3a;
  border-radius: 18px;
  padding: 26px 30px;
  background: linear-gradient(180deg, #1f1f1f, #181818);
}

.subscribe p {
  font-size: 13px;
  color: #bdbdbd;
  margin-bottom: 18px;
}

.subscribeRow {
  display: flex;
  align-items: center;
  gap: 12px;
}

.inputWrap {
  display: flex;
  align-items: center;
  gap: 8px;
  border: 1px solid #3a3a3a;
  border-radius: 30px;
  padding: 10px 14px;
  width: 100%;
}

.inputWrap input {
  background: transparent;
  border: none;
  outline: none;
  color: #fff;
  width: 100%;
}

.subscribeBtn {
  background: #fff;
  color: #000;
  border-radius: 24px;
  padding: 10px 18px;
  border: none;
  font-weight: 600;
  cursor: pointer;
}

.whatsapp {
  background: #1faa59;
  border-radius: 50%;
  width: 64px;
  height: 47px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}

/* Links */
.links {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 80px;
  margin-top: 60px;
}

.col h4 {
  margin-bottom: 14px;
}

.col a {
  display: block;
  margin-bottom: 10px;
  font-size: 14px;
  color: #cfcfcf;
}

/* Bottom */
.bottom {
  border-top: 1px solid #333;
  text-align: center;
  padding: 14px;
  font-size: 12px;
  background-color: #2c2c2c;
}

/* Responsive */
@media (max-width: 900px) {
  .top {
    flex-direction: column;
  }

  .links {
    grid-template-columns: 1fr;
    gap: 30px;
  }
}
