.center {
  text-align: center;
  width: 100%;
}

/* ===================== TITLE ===================== */
.title {
  font-family: var(--font-heading);
  font-size: calc(var(--fs-xxl) + 20px);
  font-weight: var(--fw-extrabold);
  margin: 0;
  padding-top: calc(var(--space-xl) + 48px);
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: var(--space-sm);
  color: var(--tx-title);
}

/* Gradient animated words */
.title span {
  background: linear-gradient(
    90deg,
    var(--primary-color),
    var(--secondary-color)
  );
  -webkit-background-clip: text;
  color: transparent;
  animation: loopGradient 12s ease-in-out infinite;
}

/* ===================== SUB TITLE ===================== */
.subtitle {
  margin-top: var(--space-lg);
  font-size: var(--fs-md);
  font-weight: var(--fw-bold);
  font-family: var(--font-main);
  color: var(--text-light);
}

/* ===================== ANIMATION ===================== */
@keyframes loopGradient {
  0% {
    -webkit-text-fill-color: var(--tx-title);
  }
  30% {
    -webkit-text-fill-color: transparent;
  }
  60% {
    -webkit-text-fill-color: transparent;
  }
  100% {
    -webkit-text-fill-color: var(--tx-title);
  }
}
