/* ================= ROOT ================= */

.event-section {
  width: 100%;
  margin-top: var(--space-xl);
  position: relative;
  padding: 0 6%;
  font-family: var(--font-main);
}

/* ================= HEADER ================= */

.event-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 10px;
}

.event-header h3 {
  font-size: var(--fs-lg);
  font-weight: var(--fw-bold);
  color: var(--tx-title);
}

.event_line {
  width: 100%;
  height: 2px;
  background-color: var(--text-light);
  border-radius: 10px;
  margin-bottom: 10px;
}

/* ================= BUTTONS ================= */

.see-all-btn {
  padding: 8px 35px;
  border: 1px solid var(--text-dark);
  border-radius: 20px;
  font-weight: var(--fw-semibold);
  cursor: pointer;
  background: transparent;
  font-size: var(--fs-sm);
  color: var(--text-dark);
}

/* ================= FILTER ================= */

.filter-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}

.filter-row {
  display: flex;
  gap: var(--space-sm);
  margin: var(--space-md) 0;
  padding-left: var(--space-xs);
  flex-wrap: wrap;
}

.chip {
  padding: 6px 18px;
  border: 1px solid var(--tx-placeholder);
  border-radius: 20px;
  background: var(--white);
  cursor: pointer;
  white-space: nowrap;
  font-size: var(--fs-sm);
  font-weight: var(--fw-medium);
  color: var(--text-dark);
}

.chip.active {
  background: var(--primary-color);
  color: var(--white);
  border-color: var(--primary-color);
}

/* ================= SLIDER CONTROLS ================= */

.slider-btns {
  display: flex;
  gap: var(--space-sm);
}

.nav-btn {
  width: 38px;
  height: 38px;
  border-radius: 50%;
  background: #daf0ff;
  border: none;
  cursor: pointer;
  font-size: var(--fs-md);
  transition: 0.2s;
}

.nav-btn:hover {
  background: #bfe4ff;
}

/* ================= EVENT CARDS ================= */

.event-cards {
  display: flex;
  gap: var(--space-lg);
  overflow-x: hidden;
  scroll-behavior: smooth;
  padding: 1% 0;
}

.event-card {
  min-width: 315px;
  background: var(--white);
  padding: var(--space-md);
  border-radius: 14px;
  border: 1px solid #ded6d6;
  position: relative;
  flex-shrink: 0;
}

.event-img {
  width: 100%;
  max-height: 120px;
  object-fit: cover;
  border-radius: 16px;
  margin-bottom: var(--space-lg);
  box-shadow: rgba(0, 0, 0, 0.02) 0px 1px 3px 0px, rgba(27, 31, 35, 0.15) 0px 0px 0px 1px;
}

/* ================= TAG ================= */

.tag {
  position: absolute;
  top: var(--space-md);
  left: var(--space-md);
  background: #ffd400;
  padding: 4px 8px;
  border-radius: 4px;
  font-size: var(--fs-xs);
  font-weight: var(--fw-semibold);
}

/* ================= CARD CONTENT ================= */

.event-title {
  font-size: var(--fs-md);
  font-weight: var(--fw-semibold);
  display: flex;
  justify-content: space-between;
  color: var(--tx-title);
}

.event-details p {
  font-size: var(--fs-sm);
  color: var(--text-dark);
}
.event-details div {
  display: flex;
  justify-content: space-between;
}
.event-card-bottom {
  display: flex;
  justify-content: space-between;
}
.event-card-bottom .view-icons {
  border-radius: 18px;
  background: var(--Color, #f3f3f3);
  box-shadow: 0 0 2px 0 rgba(0, 0, 0, 0.25);
  padding: 5px 15px;
}
.price-btn {
  padding: 5px 33px;
  background: #f4e8ff;
  border: 1px solid #c18aff;
  border-radius: 16px;
  font-size: var(--fs-sm);
  font-weight: var(--fw-semibold);
  color: var(--tx-title);
}

/* ================= RESPONSIVE ================= */

@media (max-width: 1421px) {
  .event-card {
    min-width: 280px;
  }
}

@media (max-width: 1150px) {
  .filter-section {
    flex-direction: column;
    align-items: flex-start;
    gap: var(--space-sm);
  }

  .slider-btns {
    align-self: flex-end;
  }

  .event-card {
    min-width: 260px;
  }

  .event-img {
    max-height: 110px;
  }
}

@media (max-width: 900px) {
  .event-section {
    padding: 0 4%;
  }

  .event-card {
    min-width: 240px;
  }

  .event-img {
    max-height: 100px;
  }

  .chip {
    padding: 5px 14px;
  }
}

@media (max-width: 600px) {
  .event-card {
    min-width: 200px;
  }

  .event-img {
    max-height: 90px;
  }

  .slider-btns {
    margin-top: var(--space-sm);
    width: 100%;
    justify-content: center;
  }
}

@media (max-width: 450px) {
  .event-card {
    min-width: 180px;
  }

  .event-img {
    max-height: 85px;
  }
}
