.root {
  width: 100%;
  padding: 8% 6% 0;
  font-family: var(--font-main);
}

.title {
  font-size: 22px;
  font-weight: 700;
  margin-bottom: 30px;
}

/* GRID */

.grid {
  display: grid;
  gap: 22px;
  grid-template-columns: repeat(4, 1fr);
}

/* CARD */

.card {
  height: 159px;
  border-radius: 16px;
  overflow: hidden;
  display: flex;
  cursor: pointer;
  position: relative;
  background: #fff;
  border: 1px solid rgba(0, 0, 0, 0.04);
  transition: transform 0.35s ease, box-shadow 0.35s ease;
  --cat-bg: #f5f5f5;
}

.card::before {
  content: "";
  position: absolute;
  inset: 0;
  background: var(--cat-bg);
  transform: translateX(-100%);
  transition: transform 1.4s cubic-bezier(.25, .8, .25, 1);
}

.card:hover::before {
  transform: translateX(0);
}

.card * {
  position: relative;
  z-index: 2;
}

.card:hover {
  transform: translateY(-4px);
  box-shadow: 0 18px 50px rgba(15,15,15,0.12);
}

/* LEFT */

.left {
  width: 45%;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--cat-bg);
}

.left img {
  width: 75%;
  object-fit: contain;
}

/* RIGHT */

.right {
  width: 55%;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
  color: #111;
}

/* CATEGORY COLORS */

.conference { --cat-bg: #ffe3bf; }
.hackathon { --cat-bg: #bde6ff; }
.webinar { --cat-bg: #ead7ff; }
.athletics { --cat-bg: #ffd4d4; }
.concerts { --cat-bg: #c9e7ff; }
.tournaments { --cat-bg: #ffeab5; }
.jobfairs { --cat-bg: #ffd0e4; }
.explore { --cat-bg: #e3d8ff; }

/* RESPONSIVE */

@media (max-width: 992px) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 720px) {
  .grid {
    grid-template-columns: 1fr;
  }

  .card {
    height: 150px;
  }

  .right {
    font-size: 18px;
  }
}
